const amap = require('../../util/amap-wx.js')
import {
	amapConfig
} from '../../util/config.js'
import Constant from '../../util/constant.js'
import mkbSearchBar from '../../components/mkb-search-bar/mkb-search-bar'
export default {
	components: {
		mkbSearchBar
	},
	computed:{
		
	},
	data() {
		return {
			keyword: '',
			amapPlugin: null,
			location: null,
			pageNo: Constant.page.pageNo,
			pageSize: Constant.page.pageSize,
			siteList: [],
			types:amapConfig.types.join(",")
		}
	},
	onLoad(option) {
		if (option.longtitude && option.latitude) {
			this.location = `${option.longtitude},${option.latitude}`;
		} else {
			this.location = amapConfig.location;
		}

		this.amapPlugin = new amap.AMapWX({
			key: amapConfig.key
		});
		this.loadData();
	},
	onReachBottom() {
		setTimeout(() => {
			this.pageNo++;
			this.loadData();
		}, 1000);
	},
	methods: {
		
		/**
		 * 置换
		 * @param {Object} dist
		 */
		transformDist(dist){
			if(!dist) return 0;
			if(dist && dist < 1000){
				return dist+'m';
			}else if(dist >= 1000){
				return (dist/1000).toFixed(2)+'km';
			}	
		},
		
		/**
		 * 搜索条件查询
		 * @param {Object} e
		 */
		confirmInput(e) {
			this.keyword = e.value;
			// uni.showToast({
			// 	icon: "none",
			// 	title: "成都已经成功keyword===" + JSON.stringify(e),
			// 	duration: 5000
			// });
			this.pageNo = Constant.page.pageNo;
			this.siteList = [];
			this.loadData();

		},

		/**
		 * 根据查询条件拉取数据
		 */
		// initData() {
		// 	let that = this;
		// 	uni.showLoading();
		// 	that.amapPlugin.getPoiAround({
		// 		querykeywords: that.keyword.split(" ").join("|"),
		// 		location: that.location,
		// 		success: function(data) {
		// 			uni.hideLoading();
		// 			if (data.poisData && data.poisData.length > 0) {
		// 				data.poisData.forEach(poi => {
		// 					that.siteList.push({
		// 						address: poi.address ? poi.address : poi.name,
		// 						distance: (poi.distance / 1000).toFixed(2),
		// 						location: poi.location
		// 					});
		// 				})

		// 			}
		// 		}
		// 	})
		// },

		/**
		 * 地址栏加载更多
		 */
		loadData() {
			let that = this;
			uni.showLoading();
			uni.request({
				url: 'https://restapi.amap.com/v3/place/around',
				method: 'GET',
				data: {
					key: amapConfig.jsKey,
					location: that.location,
					keywords: that.keyword.split(" ").join("|"),
					offset: that.pageSize,
					page: that.pageNo,
					extensitions:"all",
					types: that.types
				},
				success: (res) => {
					uni.hideLoading();
					if (res.data && res.data.pois && res.data.pois.length > 0) {
						res.data.pois.forEach(poi => {
							that.siteList.push({
								name: poi.name,
								address: poi.address,
								distance: poi.distance,
								location: poi.location
							});
						})

					}
				}
			});
		},


		select(item) {
			uni.$emit("site", {
				site: item
			});
			uni.navigateBack();
		}
	}
}
